@import "../../app.scss";



.hero{
  height: calc(100vh - 100px);
  overflow: hidden;
  background: linear-gradient(180deg, #0c0c1d, #111132);
  position: relative;

  .wrapper{
    max-width: 1066px;
    height: 100%;
    margin: auto;
  }

  .textContainer{
    width: 50%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 40px;

    @include mobile {
      height: 50%;
      width: 100%;
      gap: 20px;
      align-items: center;
      text-align: center;
      margin-top: 20px;
    }

    h2{
      font-size: 30px;
      color: #02c2b5;
      letter-spacing: 10px;

      @include mobile{
        font-size: 28px;
      }
    }

    h1{
      font-size: 50px;

      @include mobile {
        font-size: 27px;
      }
    }

    .buttons{
      display: flex;
      button{
        padding: 20px;
        border: 1px solid orange;
        border-radius: 10px;
        background-color: transparent;
        color: orange;
        margin-right: 20px;
        font-weight: 300;
        cursor: pointer;
        z-index: 2;
      }

    }

    img{
      width: 100px;
    }
  }

  .imageContainer{
    height: 100%;
    position: absolute;
    top: 0;
    right: 300px;

    @include mobile {
      height: 60%;
      width: 100%;
      top: unset;
      bottom: 0;
      right: 10px;
    }

    img{
        margin-top: -9em;
        width: 130.7%;
      @include mobile {
        margin-top: -9em;
        width: 100%;
        object-fit: contain;
      }
  
    }
  }

  .slidingTextContainer{
    position: absolute;
    font-size: 50vh;
    bottom: -120px;
    white-space: nowrap;
    color: #ffffff09;
    width: 50%;
    font-weight: bold;
    z-index: 1;
  }
}